function searchServicebyName(name) {
    return services.filter(s => s.name == name);
}
export default function ViewService() {
    const { serviceName } = useParams();
    const serviceobj = searchServicebyName(serviceName);
    return (
        <>
            <Navbar transparent />
            <main>
                <div className="relative pt-16 pb-32 flex content-center items-center justify-center min-h-screen-10">
                    <div
                        className="absolute top-0 w-full h-full bg-center bg-cover"
                        style={{
                            background: 'blue'
                        }}
                    >
                        <span
                            id="blackOverlay"
                            className="w-full h-full absolute opacity-75 bg-black"
                        ></span>
                    </div>
                </div>
                <section className="pb-20 relative block bg-white-300">
                    <div className="container mx-auto px-4 lg:pt-24 lg:pb-64">
                        <div className="flex flex-wrap text-center justify-center">
                            <div className="w-full lg:w-6/12 px-4">
                                <h2 className="text-4xl font-semibold text-black">
                                    {serviceName}
                                </h2>
                            </div>
                        </div>
                        <div className="flex flex-wrap mt-12 justify-center">
                            <div key={serviceobj[0].name} className="w-full lg:w-4/12 px-4 text-center">
                                <div className="hover:-mt-4 relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded-lg ease-linear transition-all duration-150">
                                    <img
                                        alt="..."
                                        className="align-middle border-none max-w-full h-auto rounded-lg"
                                        src={serviceobj[0].image}
                                    />
                                </div>
                                <p className="mt-2 mb-4 text-black">
                                    {serviceobj[0].description}
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
            <Footer />
        </>
    );
}
